<template>
<div>
  <Header @showcitylist="showCitySelect" :cityname="cityName"></Header>
  <div class="movie">
      <div class="wrapper" ref="wrapper">
        <ul class="content movieList"> 
            <li v-for="m in movieList">
              <!-- <a :href="m.alt"> -->
              <router-link :to="{path:'/detail/'+ m.id +''}" class="item">
                  <img :src="m.images.medium" alt="加载失败">
                  <div class="right">
                      <h3 class="m-title">{{m.title}}</h3>
                      <div class="directors">导演：{{m.directors[0].name}}</div>
                      <div class="casts">主演：<span v-for="l of m.casts">{{l.name}}<i>、</i></span></div>  
                      <div class="genres">类型：<span v-for="g of m.genres">{{g}}<i>/</i></span></div>
                      <span>豆瓣评分：{{m.rating.average}}</span>
                  </div>
              </router-link>
            </li>
            <!-- <li>gfegeg</li> 
            <li>gegeg</li>
            <li>gegege</li>  -->
        </ul>
      </div>
      <loading v-show="isOpen" :isOpen="isOpen"></loading>
      <!-- <header>
          欢迎来到豆瓣
      </header> -->
  </div>
  <Navbar></Navbar>
  <area-select :list="cityList" :show="showcity" @cityarea="citySelect"></area-select>
</div>
</template>
<script>
import Header from "./Header";
import Navbar from "./Navbar";
import loading from "./Loading";
import AreaSelect from "./AreaSelect";
// import BScroll from "better-scroll";
export default {
  name: "movie",
  components: { 
    loading: loading, 
    Header: Header, 
    Navbar: Navbar,
    AreaSelect: AreaSelect
  },
  data() {
    return {
      isOpen: true,
      mType: "in_theaters",
      cityList: [],
      movieList: [],
      showcity:false,
      cityarea:'108288',
      cityName: '北京'
    };
  },
  mounted() {
    this.loadCityList();
    this.loadData(this.cityarea,this.mType);
  },
  methods: {
    // 城市加载
    loadCityList() {
      this.$http
        .jsonp("https://api.douban.com/v2/loc/list", { params: { count: 48 } })
        .then(function(res) {
          this.cityList = res.body.locs;
        });
    },
    // 电影数据加载
    loadData(cityarea,type) {
      this.$http
        .jsonp("https://api.douban.com/v2/movie/" + type + "", {
          params: {
            apikey: "0b2bdeda43b5688921839c8ecb20399b",
            city: cityarea, //北京地区编码
            start: 0,
            count: 100
          }
        })
        .then(function(res) {
          this.movieList = res.body.subjects;
          this.isOpen = false;
          setTimeout(function() {
            const options = {
              click: true,
              taps: true
            };
          }, 1000);
        });
    },
    showCitySelect(msg){
      if(msg){
        this.showcity = msg;
      }
    },
    citySelect(msg){
      if(msg.id){
        this.cityarea = msg.id
        this.showcity = msg.show
        this.cityName = msg.name
        this.isOpen = true;
        this.movieList = [];
        this.loadData(this.cityarea,this.mType);
      }else{
        this.showcity = msg.show
      }
    }
  }
};
</script>

<style scoped>
.movie {
  height: 90vh;
}
.wrapper {
  height: 100%;
  overflow-y: scroll;
}
ul.movieList {
  /* margin: 3.3rem 0; */
  display: block;
  padding: 0;
  margin: 0;
  /* padding-top: 3rem; */
}
.movieList > li {
  display: block;
  border-bottom: 1px solid #e6e6e6;
}
.movieList li .item {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  text-decoration: none;
  padding: 1rem;
  box-sizing: border-box;
  color: #606266;
}
.movieList li .item img {
  width: 30%;
  margin-right: 1.5rem;
}
.movieList li .item .right {
  display: flex;
  flex-direction: column;
  font-size: 1.2rem;
  line-height: 2rem;
}
.movieList li .item .right h3,
.movieList li .item .right > div,
.movieList li .item .right > span {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.movieList li .item .right h3 {
  margin-top: 0;
  /* color: #cf4646; */
  color: #303133;
  font-size: 1.5rem;
  flex-direction: row;
  justify-content: flex-start;
}
.movieList li .item .right .casts {
  flex-wrap: wrap;
}
</style>



